.pageTitle {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 25px 0;
  text-transform: uppercase;
  hr {
    flex-direction: column;
    margin: 10px;
  }
  h1 {
    height: 40px;
    border-bottom: 0.5px solid;
    border-image-slice: 1;
    border-image-source: linear-gradient(
      to right,
      rgba(0, 0, 0, 0) 2%,
      black 20% 80%,
      rgba(0, 0, 0, 0) 98%
    );
  }
}
